<template>
    <div class="container">
        <router-link class="return_prev" :to="{name:'detail', params:{name:name}}">返回上一页》</router-link>
        <ul>
            <li v-for="item in dataList">
                <p>{{item.text}}</p>
                <p class="oprate">
                    <span>反馈人：王海燕 &nbsp;&nbsp;&nbsp;</span>
                    <span>反馈时间：2018/12/23 12:23:23 &nbsp;&nbsp;&nbsp;</span>
                    <span class="response"><i class="iconfont">&#xe609;</i> 回复</span>
                </p>
                <div class="response_box">
                    <p>
                        <span class="pull-left font_13">王伟</span>
                        <span class="pull-right grey font_13">2018/12/23 19:23:19</span>
                    </p>
                    <p>和我if金额为if我和覅味儿发货未付哦发货未付符合我方</p>
                </div>
            </li>
        </ul>

        <h3>我也要反馈</h3>
        <el-row :gutter="20">
            <el-col :span="2" :offset="2" class="font_13">反馈人：</el-col>
            <el-col :span="15" class="font_13" style="padding-left:0">
                <el-input v-model="person" placeholder="请输入姓名"></el-input>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="2" :offset="2" class="font_13">反馈问题：</el-col>
            <el-col :span="15" style="padding-left:0">
                <el-input v-model="problemData" placeholder="请输入相关问题反馈" type="textarea" cols="100" rows="7"></el-input><br><br>
                <el-button type="primary" v-on:click='submit()' class="pull-right">提交</el-button>
            </el-col>
        </el-row>
        
    </div>
</template>
<style scoped>
    .container{
        width: 1020px;
    }
    .el-row{
        margin-bottom: 20px;
    }
    h3{
        color: #0b70c1;
    }
    ul{
        margin: 20px 0;
        margin-bottom: 60px;
    }
    ul li{
        margin-bottom: 10px;
        padding: 5px 15px 15px;
        background: #fff;
        overflow: hidden;
        border-radius: 2px;
        box-shadow: 0 1px 3px #dcdfe6;
        box-sizing: border-box;
    }
    .oprate{
        font-size: 13px;
        color: #919191;
        text-align: right;
        margin-bottom: 0;
    }
    .response{
        color: #0b70c1;
        cursor: pointer;
    }
    .response_box{
        padding: 5px 15px 5px;
        background: #fff;
        overflow: hidden;
        border-radius: 2px;
        box-shadow: 0 1px 3px #dcdfe6;
        box-sizing: border-box;
    }
</style>
<script>
export default {

    data(){
        return{
            dataList: [],
            name: this.$route.params.name,
            problemData: '',
            person: ''
        }
    },
    mounted(){
        this.getList();
    },
    methods:{
        getList: function(){
            let _that = this;
            AJAX.default.ajaxPost(
                API.default.problemList,
                {
                    name: this.name
                },
                function(result){
                    let data = result.data;
                    if(data.status == 200){
                        _that.dataList = data.result;
                    }
                }
            )
        },
        submit: function(){
            let _that = this;
            if(!this.problemData || !this.person){
                this.$message.error('反馈人和反馈问题不能为空');
                return false;
            }
            AJAX.default.ajaxPost(
                API.default.addQuestion,
                {
                    id: this.name,
                    person: this.person,
                    text: this.problemData,
                    time: (new Date()).toLocaleString()
                },
                function(result){
                    let data = result.data;
                    if(data.status == 200){
                        _that.problemData = '';
                        _that.getList();
                        this.$message({
                            message: '提交成功',
                            type: 'success'
                        });
                    }
                }
            )
        }
    }
}
</script>


